.home {
  height: max-content;
  background: #f6f6fa;
  padding: 0px !important;
  border: 1px slid #808080;
  text-align: center;
  padding: 1rem 1rem 1rem 1rem !important;
}
.food-type {
  height: max-content;
  background: white;
  border: 1px slid #808080;
  margin-bottom: 3%;
}
.food-header {
  display: inline-flex;
}
.see-all {
  text-align: end;
}
.category {
  text-align: initial;
}
.category-hr {
  width: 96% !important;
  margin-top: 0px !important;
  margin-right: 0px !important;
  background: #e1e1e6 !important;
  margin: 2% !important;
}
.card {
  border: none !important;
}
@media (max-width: 450px) {
  .card-mob {
    width: 50% !important;
  }
}
.card-main {
  display: inline-flex !important;
}
